return _c('v-card',{class:("elevation-" + (hover ? 12 : 2)),attrs:{"min-height":"300"}},[_c('v-card-title',[_c('h3',{attrs:{"id":"Result1"}},[_vm._v("效果")])]),_c('v-divider'),_c('v-card-text',[_c('div',{staticClass:"muticolumn"},[_c('div',{staticClass:"paragraph"},[_c('h1',[_vm._v("片段一")]),_vm._v("\n                                        我们刘镇的超级巨富李光头异想天开，打算花上两千万美元的买路钱，搭乘俄罗斯联盟号飞船上太空去游览一番。\n                                        李光头坐在他远近闻名的镀金马桶上，闭上眼睛开始想象自己在太空轨道上的漂泊生涯，四周的冷清深不可测，\n                                        李光头俯瞰壮丽的地球如何徐徐展开，不由心酸落泪，这时候他才意识到自己在地球上已经是举目无亲了。\n                                        他曾经有个相依为命的兄弟叫宋钢，这个比他大一岁、比他高出一头、忠厚倔强的宋钢三年前死了，变成了一堆\n                                        骨灰，装在一个小小的木盒子里。李光头想到装着宋钢的小小骨灰盒就会感慨万千，心想一棵小树烧出来的灰也\n                                        比宋钢的骨灰多。李光头母亲在世的时候，总喜欢对李光头说：有其父必有其子。她这话指的是宋钢，她说宋钢\n                                        忠诚善良，说宋钢和他父亲一模一样，说这父子俩就像是一根藤上结出来的两个瓜。她说到李光头的时候就不说\n                                        这样的话了，就会连连摇头，她说李光头和他父亲是两个完全不同的人，是两条道上的人。直到李光头十四岁那\n                                        一年，在一个公共厕所里偷看五个女人的屁股时被人当场抓获，他母亲才彻底改变了看法，她终于知道了李光头\n                                        和他父亲其实也是一根藤上结出来的两个瓜。李光头清楚地记得他母亲当时惊恐地躲开眼睛，悲哀地背过身去，\n                                        抹着眼泪喃喃地说：“有其父必有其子啊。”李光头没有见过他的亲生父亲，在他出生的那一天，他的父亲臭气\n                                        熏天地离开了人世。母亲说他父亲是淹死的。李光头问是怎么淹死的：是在小河里淹死的，还是在池塘里淹死的？\n                                        或者是在井里淹死的？他的母亲一声不吭。\n                                    ")]),_c('div',{staticClass:"paragraph"},[_c('h1',[_vm._v("片段二")]),_vm._v("\n                                        后来李光头在厕所里偷看女人屁股被生擒活捉，用现在的时髦说法是闹\n                                        出了绯闻，李光头在厕所里的绯闻曝光以后，他在我们刘镇臭名昭著以后，才知道自己和父亲真是一根藤上结出来\n                                        的两个臭瓜。他的那个生父亲爹就是在厕所里偷看女人屁股时，不慎掉进粪池里淹死了。我们刘镇的男女老少乐开\n                                        了怀笑开了颜，张口闭口都要说上一句：有其父必有其子。只要是棵树，上面肯定挂着树叶；只要是个刘镇的人，\n                                        这人的嘴边就会挂着那句口头禅。连吃奶的婴儿牙牙学语时，也学起了这句拗口的文言文。人们对着李光头指指点\n                                        点，窃窃私语，掩嘴而笑，李光头却是一脸无辜的表情，若无其事地走在大街小巷。他心里嘿嘿笑个不停，那个时\n                                        候他快十五岁了，他已经知道了男人是个什么东西。现在满世界都是女人的光屁股晃来晃去，在电视里和电影里，\n                                        在VCD和DVD里，在广告上和画报上，在写字用的圆珠笔上，在点烟用的打火机上……什么样的屁股都有，进口屁股\n                                        国产屁股，白的黄的黑的还有棕色的，大的小的胖的瘦的，光滑的粗糙的，幼的老的假的真的，琳琅满目目不暇接。\n                                        现在女人的光屁股不值钱了，揉一揉眼睛就会看到，打一个喷嚏就会撞上，走路拐个弯就会踩着。在过去可不是这\n                                        样，在过去那是金不换银不换珠宝也不换的宝贝，在过去只能到厕所里去偷看，所以就有了像李光头这样当场被抓\n                                        获的小流氓，有了像李光头父亲那样当场丢了性命的大流氓。那时候的公共厕所和现在的不一样，现在的公共厕所\n                                        里就是用潜望镜也看不见女人的屁股了。那时候的公共厕所男女中间只是隔了一堵薄薄的墙，下面是空荡荡的男女\n                                        共有的粪池，墙那边女人拉屎撒尿的声音是真真切切，把你撩拨得心驰神往。你就将头插了进去，那本来应该是你\n                                        的屁股坐进去的地方，你欲火熊熊就把头插了进去，你的双手紧紧抓住木条，你的双腿和肚子紧紧夹住挡板，恶臭\n                                        熏得你眼泪直流，粪蛆在你的四周胡乱爬动，你也毫不在乎，你的动作就像是游泳选手比赛时准备跳水的模样，你\n                                        的头和身体插得越深，你看到的屁股面积也就越大。\n                                    ")]),_c('div',{staticClass:"paragraph"},[_c('h1',[_vm._v("片段三")]),_vm._v("\n                                        李光头那次一口气看到了五个屁股，一个小屁股，一个胖屁股，\n                                        两个瘦屁股和一个不瘦不胖的屁股，整整齐齐地排成一行，就像是挂在肉铺里的五块猪肉。那个胖屁股像是新鲜的\n                                        猪肉，两个瘦屁股像是腌过的咸肉，那个小屁股不值一提，李光头喜欢的是那个不瘦不胖的屁股，就在他眼睛的正\n                                        前方，五个屁股里它最圆，圆得就像是卷起来一样，绷紧的皮肤让他看见了上面微微突出的尾骨。他心里怦怦乱跳，\n                                        他想看一看尾骨另一端的阴毛，想看一看阴毛是从什么样的地方生长出来的，他的身体继续探下去，他的头继续钻\n                                        下去，就在他快要看到女人的阴毛时，他被生擒活捉了。有一个名叫赵胜利的人这时恰好跑进了厕所，他是我们刘\n                                        镇的两大才子之一，他看到有个人的脑袋和上身插了下去，立刻知道是怎么回事了。他一把抓住了李光头后背的衣\n                                        服，像是拔萝卜似的一把将李光头拔了上来。当时的赵胜利二十多岁，已经在我们县文化馆的油印杂志上发表了一\n                                        首四行小诗，为此他拥有了一个名人的绰号——赵诗人。赵诗人在厕所里捉拿了李光头以后，兴奋得满脸通红，他把\n                                        十四岁的李光头提到了厕所外面，滔滔不绝地训斥起了李光头，他在训斥的时候仍然是满嘴的诗情画意：“田野里的\n                                        油菜花金黄一片，你不去看；小河里的鱼儿在水中戏耍，你不去看；天空蔚蓝浮云洁白多么美丽，你不抬头去看；\n                                        厕所里臭气冲天，你偏偏要低头塞进去看……”\n                                    ")])])])],1)}}])})],1),_c('v-flex',{attrs:{"sm12":""}},[_c('v-hover',{scopedSlots:_vm._u([{key:"default",fn:function(ref){
